body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
}

.navbar {
    background: #2883FF;
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
}

.navbar a {
    font-size: 18px;
    font-weight: 500;
    color: #ffffff;
    text-decoration: none;
    margin: 0 20px;
}

.navbar a img {
    width: 40px;
    height: 40px;
    padding: 5px;
}

.cslr {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
}

.text-section,
.image-section {
    flex: 1;
    padding: 20px;
    box-sizing: border-box;
}

.button {
    background: #2883FF;
    color: #ffffff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.card img {
    width: 149px;
    height: 149px;
}

.tv-section {
    margin: 50px 0;
}

.tv-section button {
    margin: 0 5px;
    width: 25%;
    padding: 10px;
}

.ftco-gallery {
    background-image: url('./images/subs.png');
    background-size: cover;
    background-position: center;
    padding: 20px; /* Add padding for spacing around the form */
}

/* Style the input type box */
.ftco-gallery .form-control {
    border-radius: 8px; /* Add border-radius for rectangular corners */
}

.ftco-footer {
    background-image: url('./images/footer.png');
    background-size: cover;
    background-position: center;
    color: #ffffff;
    padding: 50px 0;
    text-align: center;
}

@media (max-width: 767.98px) {
    .cslr {
        flex-direction: column;
        align-items: flex-start;
        padding: 10px;
    }

    .navbar {
        flex-direction: column;
        align-items: flex-start;
        padding: 10px;
    }

    .navbar a {
        margin: 10px 0;
    }

    .text-section,
    .image-section {
        padding: 10px;
    }

    .tv-section button {
        width: 100%;
        margin: 10px 0;
    }
}


.arrow-circle {
    width: 40px;
    height: 40px;
    background-color: #2883FF;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
}

.arrow-circle span {
    color: white;
    font-size: 24px;
}
