.carousel-caption {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.content-container {
    background: rgba(255, 255, 255, 0.8);
    padding: 20px;
    border-radius: 10px;
    position: relative;
    text-align: left;
}

.icons-container {
    position: absolute;
    bottom: 10px;
    right: 10px;
    display: flex;
}

.material-symbols-outlined {
    font-family: 'Material Symbols Outlined', sans-serif;
    font-size: 24px;
    margin-right: 10px;
}

/* CSS File */
.icon-square {
    width: 50px; /* Set your desired width */
    height: 50px; /* Set your desired height */
    background-color: #ffffff; /* Set your desired background color */
    border: 2px solid #000000; /* Set your desired border color and thickness */
    margin: 5px; /* Set margin as needed */
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px; /* Set your desired font size for the symbols */
}

/* Additional CSS for larger symbols */
.material-symbols-outlined {
    font-size: 30px; /* Set your desired font size for the symbols */
}




.email-input-container {
    position: relative;
    width: 70%; /* Reduce the size of the input container by 30% */
    margin: 0 auto; /* Center the input container horizontally */
}

.email-input {
    width: 100%; /* Make the input field fill the container */
}

.email-arrow-icon {
    position: absolute;
    right: 10px; /* Adjust the right distance as needed */
    top: 50%; /* Center the arrow vertically */
    transform: translateY(-50%);
    width: 30px;
    height: 30px;
}
